import React, { useEffect, useState } from 'react';
import Sidebar from '../components/Sidebar';
import { getReservations } from '../services/reservation';
import { Reservation } from '../types/reservation';
import { toast } from 'react-toastify';
import styles from '../styles/Dashboard.module.css'; // Importando as classes do CSS Module

const Dashboard: React.FC = () => {
  const [reservations, setReservations] = useState<Reservation[]>([]);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const reservationsData = await getReservations();
        setReservations(reservationsData);
      } catch (error) {
        toast.error('Erro ao carregar as reservas!');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  return (
    <div className={styles.dashboardContainer}>
      <Sidebar />
      <div className={styles.dashboardContent}>
        <h1 className={styles.dashboardTitle}>Dashboard</h1>

        {/* Resumo de Reservas */}
        <div className={styles.summaryContainer}>
          <div className={styles.summaryBox}>
            <h3>Total de Reservas</h3>
            <p>{reservations.length} reservas realizadas</p>
          </div>
        </div>

        {/* Tabela de Reservas */}
        <div className={styles.tableContainer}>
          <h2>Últimas Reservas</h2>
          {loading ? (
            <p className={styles.loading}>Carregando...</p>
          ) : (
            <table className={styles.table}>
              <thead>
                <tr>
                  <th className={styles.th}>Ambiente</th>
                  <th className={styles.th}>Data</th>
                  <th className={styles.th}>Status</th>
                  <th className={styles.th}>Ações</th>
                </tr>
              </thead>
              <tbody>
                {reservations.slice(0, 5).map((reservation) => (
                  <tr key={reservation.id}>
                    <td className={styles.td}>{reservation.environment}</td>
                    <td className={styles.td}>{new Date(reservation.date).toLocaleString()}</td>
                    <td className={styles.td}>{reservation.status}</td>
                    <td className={styles.td}>
                      <button className={styles.button} onClick={() => alert(`Editando reserva de ${reservation.environment}`)}>Editar</button>
                      <button className={`${styles.button} ${styles.buttonMarginLeft}`} onClick={() => alert(`Cancelando reserva de ${reservation.environment}`)}>Cancelar</button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
